/* Profile sidebar */
.profile-sidebar{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 19%;
    height: 90vh;
    background: white;
    padding: 1em 0;
}


.profile-sidebar-top{
    position: relative;
    padding: 1em;
    width: 100%;
    height: 25%;
    border-bottom: 1px solid var(--border-clr);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}
.profile-sidebar-top-profile{
    width: 3em;
    height: 3em;
    border-radius: 50%;
    border: 1px solid var(--border-clr);
    display: flex;
    justify-content: center;
    align-items: center;
}
.profile-sidabar-close-btn{
    background: white;
    border: none;
    color: var(--secondary-clr);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -1px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3em;
    font-size: 1.2rem;
    cursor: pointer;
    z-index: 25;
}

.profile-sidebar-options-top-links{
    display: flex;
    flex-direction: column;
}
.profile-sidebar-options-top-links-btn, .profile-sidebar-options-top-links-btn-active{
    position: absolute;
    color: var(--font-clr);
    right: 1em;
    background: transparent;
    border: none;
    transform: rotate(180deg);
    cursor: pointer;
    font-size: 1.4rem;
    transition: transform 0.3s;
}
.profile-sidebar-options-top-links-btn-active{
    transform: rotate(360deg);
}
.profile-sidebar-options-top-links > div:nth-child(1){
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    padding: 0.4em 0.4em 0.4em 1em;
}
.profile-sidebar-options-top-links > div:nth-child(1):hover{
    background: var(--third-clr);
}
.profile-sidebar-options-top-links > div:nth-child(1) > div{
    display: flex;
    align-items: center;
    gap: 0.3em;
    width: 100%;
}
.profile-sidebar-options-top-links > div:nth-child(1) > div p{
    color: var(--font-clr);
    font-size: 0.9rem;
    font-weight: 400;
    margin: 0;
    user-select: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.profile-sidebar-options-top-links > div:nth-child(2){
    padding: 0 0 0 1.8em;
}



.profile-sidebar-options{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.profile-sidebar-options-top, .profile-sidebar-options-bottom{
    display: flex;
    flex-direction: column;
    padding: 0 0.5em;
}
.profile-sidebar-options-top-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1em;
}
.profile-sidebar-options-top-header p{
    text-align: left;
    color: var(--font-clr);
    font-size: 0.8rem;
}
.profile-sidebar-options-top-header button{
    border-radius: 5px;
    background: var(--secondary-clr);
    border: none;
    color: var(--main-clr);
    cursor: pointer;
    padding: 0.5em 1em;
}
.profile-sidebar-options-top-header button:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
    border-color: var(--secondary-clr);
}
.profile-sidebar-icons{
    color: var(--font-clr);
    font-size: 1.2rem;
    font-weight: 300;
}


.profile-sidebar-options-btn, .profile-sidebar-options-btn-active{
    width: 100%;
    text-decoration: none;
    position: relative;
    background: transparent;
    border: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
    border-radius: 5px;
    padding: 0.5em 0.5em 0.5em 1em;
    color: var(--font-clr);
    font-weight: 500;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s;
}
.profile-sidebar-options-btn:hover{
    background: var(--third-clr);
}
.profile-sidebar-options-btn-active{
    background: #ceb5ff;
}
.profile-sidebar-options-btn-icon{
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile-sidebar-options-count{
    position: absolute;
    right: 0.8em;
    color: var(--secondary-clr);
    font-size: 0.7rem;
    background: var(--third-clr);
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    justify-content: center;
    align-items: center;
}
#profile-sidebar-options-btn-alert{
    color: var(--secondary-clr);
    font-size: 0.7rem;
    position: absolute;
    right: 2em;
    background: var(--third-clr);
    width: 18px;
    height: 18px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}


/* Sidebar shrinked */
.profile-sidebar-shrinked{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 6%;
    height: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid var(--border-clr);
    box-shadow: var(--material-shadow);
    padding: 1em 0;
}
.profile-sidebar-shrinked-top, .profile-sidebar-shrinked-bottom{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.profile-sidebar-shrinked-bottom{
    position: absolute;
    bottom: 0;
    left: 0;
}
.profile-sidebar-shrinked-link{
    position: relative;
    width: 100%;
    background: transparent;
    border-radius: 5px;
    border: none;
    padding: 0.5em;
    font-size: 22px;
    color: var(--font-clr);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.profile-sidebar-shrinked-link:hover{
    background: var(--main-clr);
    color: var(--secondary-clr);
}
#profile-sidebar-shrinked-link-active{
    position: absolute;
    left: 0;
    top: 0;
    width: 5px;
    height: 100%;
    border-radius: 0 25px 25px 0;
    background: var(--secondary-clr);
}
#profile-sidebar-shrinked-link-label{
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    background: var(--font-clr);
    padding: 0.3em;
    border-radius: 2px;
    color: var(--main-clr);
    font-size: 11px;
    z-index: 2;
}
.profile-sidebar-shrinked-close-btn{
    width: max-content;
    font-size: 18px;
    margin-bottom: 1em;
    border-radius: 5px;
    border: none;
    padding: 0.5em;
    background: var(--secondary-clr);
    transform: rotate(180deg);
    color: var(--main-clr);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .profile-sidebar{
        display: none;
    }
}

@media only screen and (max-width: 480px){
    .profile-sidebar{
        display: none;
    }

    .profile-sidebar-options-btn, .profile-sidebar-options-btn-active{
        font-size: 1rem;
    }
    .profile-sidebar-options-btn-icon{
        font-size: 1.5rem;
    }
}