.billing-plans{
    width: 100%;
    min-height: 150vh;
    display: flex;
    flex-direction: column;
    gap: 2em;
    padding: 2em;
    border-left: 1px solid var(--border-clr);
    background: var(--main-light);
    position: relative;
}
.billing-plans-header{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.billing-plans-header h1{
    text-align: left;
    font-size: 1.2rem;
    margin: 0;
    font-weight: 400;
}
.billing-plans-header p{
    text-align: left;
    margin: 0;
    font-size: 0.9rem;
    color: var(--font-light);
}


.billing-plans-current-plan{
    width: max-content;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.billing-plans-current-plan p{
    text-align: left;
    text-transform: capitalize;
    margin: 0;
}
.billing-plans-current-plan span{
    text-align: left;
    padding: 0.3em 0.6em;
    background: var(--secondary-clr);
    width: max-content;
    border-radius: 5px;
    font-size: 0.8rem;
    color: var(--main-clr);
    margin: 0;
}
.billing-plans-current-plan small{
    color: var(--font-light);
    text-align: left;
}


.billing-plans-wrapper{
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
}