#subscription-complete-billing-form{
    display: flex;
    flex-direction: column;
    box-shadow: var(--material-shadow-intense);
    padding: 1em;
    border-radius: var(--radius);
}
#subscription-complete-billing-form label{
    text-align: left;
    font-size: 0.8rem;
    font-weight: 500;
    margin-top: 1.5em;
    margin-bottom: 0.5em;
}
#subscription-complete-billing-form select{
    width: 100%;
    border-radius: 5px;
    border: 1px solid var(--border-clr);
    padding: 1em;
    color: var(--font-light);
    cursor: pointer;
}
#subscription-complete-billing-form select option{
    padding: 0.5em;
    color: var(--font-clr);
    font-size: 1.1rem;
}
#subscription-complete-billing-form select option:focus{
    outline: 2px solid var(--secondary-light);
}
#subscription-complete-billing-form input{
    background: transparent;
    border: 1px solid var(--border-clr);
    border-radius: 5px;
    padding: 1em;
}
#subscription-complete-billing-form input:focus{
    outline: 2px solid var(--secondary-light);
}
#subscription-complete-billing-form button{
    background: var(--secondary-light);
    border: none;
    border-radius: var(--radius);
    color: var(--main-clr);
    padding: 1em;
    margin-top: 2em;
    cursor: pointer;
    transition: background 0.3s;
}
#subscription-complete-billing-form button:hover{
    background: var(--intense-clr);
}