.subscription-complete{
    width: 100%;
    min-height: 100vh;
    display: flex;
    padding-top: 5em;
}


#subscription-complete-form{
    width: 60%;
    display: flex;
    flex-direction: column;
    gap: 2em;
    padding: 2em 4em;
    background: var(--main-light);
}

.subscription-complete-img{
    position: fixed;
    bottom: 0;
    right: 0;
    width: 40%;
    height: 85vh;
    overflow: hidden;
    border-radius: 25px 0 0 25px;
}
.subscription-complete-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.subscription-complete-payment-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}


.subscription-complete-choice, .subscription-complete-choice-checked{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    transition: background 0.3s;
}
.subscription-complete-choice::before{
    content: '1';
    position: absolute;
    left: -40px;
    top: 5px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--secondary-light);
    color: var(--main-clr);
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.subscription-complete-choice-checked::before{
    content: '1';
    position: absolute;
    left: -40px;
    top: 5px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--secondary-light);
    color: var(--main-clr);
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.subscription-complete-choice h1, .subscription-complete-choice-checked h1{
    width: 100%;
    font-size: 1.5rem;
    text-transform: capitalize;
    text-align: left;
    font-weight: 400;
    color: var(--font-clr);
    margin: 0;
}
.subscription-complete-choice h1 span, .subscription-complete-choice-checked h1 span{
    text-transform: lowercase;
}
.subscription-complete-choice-list{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    gap: 0.5em;
}
.subscription-complete-choice-list li{
    text-align: left;
    list-style-type: none;
    color: var(--font-light);
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.3em;
}
.subscription-complete-choice h5, .subscription-complete-choice-checked h5{
    text-align: left;
    margin: 0;
    margin-left: 2em;
    font-weight: 300;
    color: var(--secondary-clr);
}

.subscription-complete-choice-single, .subscription-complete-choice-single-active{
    width: 100%;
    border-radius: var(--radius);
    border: 1px solid var(--border-clr);
    padding: 2em 1em;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.subscription-complete-choice-single:hover, .subscription-complete-choice-single-active:hover{
    background: var(--main-clr);
}
.subscription-complete-choice-single-active{
    box-shadow: var(--material-shadow);
    background: var(--main-clr);
}
.subscription-complete-choice-single-top{
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.subscription-complete-choice-single-top input[type='radio']:after {
    width: 12px;
    height: 12px;
    border-radius: 15px;
    top: -1px;
    left: -1px;
    position: relative;
    background-color: #ffffff;
    border: 1px solid var(--font-clr);
    content: '';
    display: inline-block;
    visibility: visible;
    cursor: pointer;
}

.subscription-complete-choice-single-top input[type='radio']:checked:after {
    width: 8px;
    height: 8px;
    border-radius: 15px;
    top: 0px;
    left: -0px;
    position: relative;
    background-color: var(--secondary-clr);
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid white;
}
.subscription-complete-choice-single-top p span, .subscription-complete-choice-single-active p span{
    background: var(--third-clr);
    font-size: 0.7rem;
    color: var(--secondary-clr);
    padding: 0.5em 1em;
    border-radius: 15px;
}
.subscription-complete-choice-single small, .subscription-complete-choice-single-active small{
    text-align: left;
    color: var(--font-light);
    font-size: 0.8rem;
    margin-left: 2em;
}


.subscription-complete-choice-price{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    width: 100%;
    margin-top: 2em;
}
.subscription-complete-choice-price > div{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.subscription-complete-choice-price p{
    font-size: 0.8rem;
    color: var(--font-light);
}
.subscription-complete-choice-price small{
    color: var(--font-clr)
}


.subscription-complete-choice button{
    width: 100%;
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-radius: var(--radius);
    padding: 1em 1em;
    cursor: pointer;
    transition: background 0.3s;
}
.subscription-complete-choice button:hover{
    background: var(--intense-clr);
}


/* Finish Payment */
.subscription-complete-pay, .subscription-complete-pay-error{
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-top: 1em;
}
.subscription-complete-pay-error > div{
    padding: 0.5em;
    border-bottom: 2px solid var(--fail-clr);
}
.subscription-complete-pay > div{
    display: flex;
    align-items: center;
    gap: 1em;
}
.subscription-complete-pay > div small{
    font-size: 0.8rem;
    color: var(--font-light);
}
.subscription-complete-pay-btn{
    width: 100%;
    background: var(--secondary-clr);
    padding: 1em;
    border: none;
    border-radius: var(--radius);
    color: var(--main-clr);
    cursor: pointer;
    transition: background 0.3s;
}
.subscription-complete-pay-btn:hover{
    background: var(--intense-clr);
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .subscription-complete-img{
        display: none;
    }
    #subscription-complete-form{
        width: 100%;
        padding: 1em;
    }
}

@media only screen and (max-width: 480px){
    .subscription-complete-img{
        display: none;
    }
    .subscription-complete-content{
        width: 100%;
        padding: 2em 1em;
    }
}