.register-payment{
    position: relative;
    min-height: 100vh;
    padding: 4em 4em 8em 4em;
    display: flex;
    align-items: center;
    justify-content: center;
}



.checkout-form{
    width: 50%;
    height: max-content;
    box-shadow: var(--material-shadow);
    padding: 2em;
    display: flex;
    flex-direction: column;
    gap: 1em;
    border-radius: var(--radius);
    font-size: 0.8rem;
    color: var(--font-light);
}
.checkout-form h1{
    font-size: 1.5rem;
    font-weight: 400;
    color: var(--font-title-clr);
    margin: 0 0 2em 0;
    text-align: left;
}
.checkout-form form{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.checkout-form form small{
    width: 100%;
    text-align: left;
    color: var(--font-light);
    font-size: 0.9rem;
}
.checkout-form form label{
    text-align: left;
    color: var(--font-landing-clr);
    font-size: 0.9rem;
    margin-bottom: 0.5em;
}
#checkout-form-card, #checkout-form-expiry, #checkout-form-cvc, #checkout-form-card-wrapper, .checkout-form input{
    border: 1px solid var(--border-clr);
    border-radius: 5px; 
    padding: 1em;
}
.checkout-form input:disabled{
    background: var(--main-light);
}
.checkout-form-elemet-wrapper{
    width: 100%;
    display: flex;
    gap: 1em;
}
.checkout-form-elemet-wrapper > div{
    display: flex;
    flex-direction: column;
    width: 50%;
}
.checkout-form button{
    width: 100%;
    border: none;
    background: var(--secondary-clr);
    padding: 1em;
    border-radius: 5px;
    color: var(--main-clr);
    cursor: pointer;
}
.checkout-form button:hover{
    background: var(--intense-clr);
}
.checkout-form-total{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.checkout-form-total p{
    color: var(--font-landing-clr);
}
.checkout-form-total small{
    color: var(--font-light);
}

/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .register-payment{
        padding: 1em 1em 8em 1em;
    }
    .checkout-form{
        width: 100%;
    }
}