.billing{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1em 2em 5em 1em;
    gap: 1em;
}
.billing h1{
    font-weight: 400;
    font-size: 1.2rem; 
    text-align: left;
    margin: 0 0 1em 0;
}
.billing-nav{
    display: flex;
    align-items: center;
}
.billing-nav-btn, .billing-nav-btn-active{
    background: transparent;
    border: none;
    padding: 0.5em 1em;
    border-radius: 15px;
    cursor: pointer;
}
.billing-nav-btn-active{
    background: var(--secondary-clr);
    color: var(--main-clr);
}


.billing-overview, .billing-incoices, .payment-details{
    width: 100%;
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    gap: 1em;
}


/* Overview */
.billing-plan-overview{
    width: 100%;
    background: var(--main-clr);
    border-radius: 5px;
    padding: 2em;
    display: flex;
    flex-direction: column;
    gap: 2em;
}
.billing-plan-overview-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.billing-plan-overview-top h5{
    font-size: 1.2rem;
    font-weight: 400;
    text-align: left;
    margin: 0;
}
.billing-plan-overview-top-link{
    font-size: 1rem;
}
.billing-plan-overview-content{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.billing-plan-overview-content > div{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.billing-plan-overview-content > div p{
    text-align: left;
    color: var(--font-light);
    font-size: 1rem;
}
.billing-plan-overview-content > div small{
    text-align: left;
    color: var(--font-clr);
    font-size: 0.9rem;

}
.billing-plan-overview-content li{
    color: var(--secondary-clr);
    text-align: left;
    list-style-type: none;
    font-size: 0.8rem;
}


.billing-details{
    width: 100%;
    background: var(--main-clr);
    border-radius: 5px;
    display: flex;
    padding: 2em;
    flex-direction: column;
    gap: 2em;
}
.billing-details-top{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.billing-details-top h5{
    font-size: 1.2rem;
    font-weight: 400;
    text-align: left;
    margin: 0;
}
.billing-details-top button{
    width: max-content;
}


.billing-details-content{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.billing-details-content p{
    text-align: left;
    color: var(--font-light);
}
.billing-details-content small{
    text-align: left;
    color: var(--font-clr);
}
.billing-details-content li{
    font-size: 0.9rem;
    list-style-type: none;
    text-align: left;
    color: var(--font-clr);
}


/* Invoices */
.billing-invoices-list{
    background: var(--main-clr);
}
.billing-invoices-list-top{
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    border: 1px solid var(--border-clr);
    border-radius: var(--radius) var(--radius) 0 0;
    background: var(--main-clr);
}
.billing-invoices-list-top div{
    padding: 0.3em 0.6em;
    font-size: 0.8rem;
    background: var(--main-clr);
    text-align: left;
}
.billing-invoices-list-empty{
    background: var(--main-clr);
    padding: 0.5em;
    text-align: left;
    font-size: 0.9rem;
    color: var(--font-light);
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    
}

@media only screen and (max-width: 480px){
    .billing{
        padding: 1em 1em 5em 1em;
    }
    .billing-plan-overview, .billing-details{
        padding: 1em;
    }
    .billing-details-content small{
        font-size: 0.8rem;
    }
}