.register-plans{
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 8em 4em;
}


.register-plans-content{
    display: flex;
    gap: 2em;
}

/* Checkout info */
.checkout-info{
    width: 50%;
    display: flex;
    flex-direction: column;
   box-shadow: var(--material-shadow);
   background: var(--main-light);
   border-radius: var(--radius);
   padding: 0 2em;
}
.checkout-info-content{
    padding: 2em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.checkout-info-content h5{
    font-size: 1.5rem;
    line-height: 1.1em;
    font-weight: 400;
    text-align: left;
    color: var(--font-title-clr);
    margin: 0;
}
.checkout-info-content p{
    text-align: left;
    color: var(--font-landing-clr);
    margin: 0 0 0.5em 0;
    font-size: 0.9rem;
    line-height: 1.5em;
}
.checkout-info-content > div{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.checkout-info-content li{
    text-align: left;
    font-size: 0.9rem;
    color: var(--font-light);
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
#checkout-plans-icon{
    color: var(--success-clr);
}


.register-plans-options{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.register-plans-option{
    width: 100%;
    box-shadow: var(--material-shadow);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2em;
    border-radius: 5px;
}
.register-plans-option:hover{
    background: var(--main-light);
}
.register-plans-options-link{ 
    background: var(--secondary-clr);
    color: var(--main-clr);
    text-decoration: none;
    padding: 0.5em 1em;
    border-radius: 5px;
}
.register-plans-option > div{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.register-plans-option h5{
    text-align: left;
    color: var(--font-title-clr);
    margin: 0;
    font-size: 1.2rem;
    font-weight: 400;
}
.register-plans-option h5 span{
    background: var(--third-clr);
    font-size: 0.8rem;
    color: var(--font-landing-clr);
    border-radius: 15px;
    padding: 0.3em 0.6em;
}
.register-plans-option p{
    text-align: left;
    color: var(--font-landing-clr);
    width: 100%;
    margin: 0;
}
.register-plans-options small{
    text-align: left;
    margin: 0;
    color: var(--font-light);
}

/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .register-plans{
        padding: 8em 1em;
    }
    .register-plans-content{
        flex-direction: column;
    }
    .register-plans-options, .checkout-info{
        width: 100%;
    }
    .register-plans-option{
        flex-direction: column;
        gap: 1em;
    }
    .register-plans-options-link{
        width: 100%;
    }
}