.checkout{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
    padding: 0 4em 8em 4em;
}
.checkout-top{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2em 0;
}
.checkout-top h1{
    color: var(--font-title-clr);
    text-align: left;
    margin: 0;
}

.checkout-content{
    width: 100%;
    display: flex;
    gap: 1em;
}
.checkout-content-payment{
    width: 50%;
    padding: 0 2em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}

.checkout-form{
    height: max-content;
    box-shadow: var(--material-shadow);
    padding: 2em;
    display: flex;
    flex-direction: column;
    gap: 1em;
    border-radius: var(--radius);
    font-size: 0.8rem;
    color: var(--font-light);
}
.checkout-form h1{
    font-size: 1.5rem;
    font-weight: 400;
    color: var(--font-title-clr);
    margin: 0 0 2em 0;
    text-align: left;
}




/* Checkout plans options */
.checkout-plans-options{
    background: var(--main-clr);
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 2em;
    box-shadow: var(--material-shadow);
    border-radius: var(--radius);
}
.checkout-plans-options p{
    text-align: left;
    width: 100%;
    color: var(--font-landing-clr);
}
.checkout-plans-options small{
    text-align: left;
    font-size: 0.8rem;
    color: var(--font-light);
}
.checkout-plans-options-items{
    display: flex;
    gap: 1em;
}
.checkout-plans-option, .checkout-plans-option-active{
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 1em;
    border-radius: 5px;
    transition: transform 0.2s;
}
.checkout-plans-option:hover{
    transform: scale(1.1);
}
.checkout-plans-option-active{
    background: var(--secondary-clr);
}
.checkout-plans-option-active p{
    color: var(--main-clr);
}


/* Checkout Complete */
.checkout-complete{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 8em;
}
.checkout-complete h1{
    color: var(--font-title-clr);
    text-align: left;
    margin: 0;
}
.checkout-complete p{
    text-align: left;
    color: var(--font-landing-clr);
    margin: 0;
}
.checkout-complete-link{
    width: max-content;
    background: var(--secondary-clr);
    color: var(--main-clr);
    padding: 0.5em 1em;
    border-radius: var(--radius);
    text-decoration: none;
    margin-top: 1em;
    cursor: pointer;
}
.checkout-complete-link:hover{
    background: var(--intense-clr);
}


/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .checkout{
        padding: 1em 1em 6em 1em;
    }
    .checkout-top{
        padding: 1em 0;
    }
    .checkout-top h1{
        font-size: 2rem;
    }
    .checkout-payment{
        flex-direction: column;
    }
    .checkout-promo, .checkout-form{
        width: 100%;
    }
    .checkout-promo-info, .checkout-promo-bottom, .checkout-form{
        padding: 1em;
    }
}