.payment-details-card{
    padding: 2em;
    background: var(--main-clr);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.payment-details-card-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.payment-details-card-top button{
    padding: 0.5em 1em;
    border-radius: 5px;
    border: 1px solid var(--border-clr);
    cursor: pointer;
}
.payment-details-card-top button:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}
.payment-details-card-top h5{
    margin: 0;
    font-weight: 400;
    font-size: 1rem;
}
.payment-details-card-content{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.payment-details-card-content p{
    text-align: left;
    color: var(--font-light);
}
.payment-details-card-content span{
    color: var(--font-light);
    font-size: 0.8rem;
    background: var(--main-light);
    border: 1px solid var(--border-clr);
    padding: 0.2em 0.4em;
    border-radius: 5px;
}
.payment-details-card-content small{
    text-align: left;
    color: var(--font-clr);
}
.payment-details-card-content li{
    list-style-type: none;
    text-align: left;
    font-size: 0.9rem;
    color: var(--font-clr);
}