.subscription-complete-payment, .subscription-complete-billing{
    position: relative;
    width: 100%;
    background: var(--main-clr);
    padding: 1em;
    border-radius: var(--radius);
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.subscription-complete-payment::before{
    content: '3';
    position: absolute;
    left: -40px;
    top: 25px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--secondary-light);
    color: var(--main-clr);
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.subscription-complete-billing::before{
    content: '2';
    position: absolute;
    left: -40px;
    top: 25px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: var(--secondary-light);
    color: var(--main-clr);
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.subscription-complete-payment-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.subscription-complete-payment-top h1{
    font-size: 1rem;
    font-weight: 600;
    color: var(--font-clr);
}


.subscription-complete-form, .subscription-complete-form-active{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    border-radius: var(--radius);
    padding: 1em;
    box-shadow: var(--material-shadow-intense);
    cursor: pointer;
}
.subscription-complete-form-active{
    border: 4px solid var(--main-light);
}
.subscription-complete-form-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.subscription-complete-form-top div{
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.subscription-complete-form-top div img{ 
    height: 1em;
}
.subscription-complete-form-top p{
    font-size: 0.8rem;
    text-align: left;
    font-weight: 500;
    color: var(--font-clr);
}

.subscription-complete-form-fields{
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.subscription-complete-form-input{
    display: flex;
    align-items: center;
    gap: 0.5em;
    border: 1px solid var(--font-light);
    border-radius: var(--radius);
    padding: 0 1em;
}
.subscription-complete-form-input:focus-within{
    border: 2px solid var(--secondary-clr);
}
.subscription-complete-form-input input{
    width: 90%;
    border: none;
    outline: none;
    padding: 1em;
    background: transparent;
}
.subscription-complete-form-input-date{
    width: 50%;
    display: flex;
    align-items: center;
    gap: 0.5em;
    border: 1px solid var(--font-light);
    border-radius: var(--radius);
    padding: 0 1em;
}
.subscription-complete-form-input-date:focus-within{
    border: 2px solid var(--secondary-clr);
}
.subscription-complete-form-input-date input{
    width: 30%;
    background: transparent;
    border: none;
    outline: none;
    padding: 1em;
}
.subscription-complete-form-half{
    display: flex;
    align-items: center;
    gap: 1em;
}